"use client"

import { useEffect, useState } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Download, Upload, Zap, Crown } from "lucide-react"
import Link from "next/link"
import { createBrowserClient } from "@/lib/supabase/client"
import type { PlanType } from "@/lib/plans"

interface ProfileLimitsCardProps {
  userId: string
  userPlan: PlanType
}

export function ProfileLimitsCard({ userId, userPlan }: ProfileLimitsCardProps) {
  const [remainingDownloads, setRemainingDownloads] = useState<number | null>(null)
  const [remainingUploads, setRemainingUploads] = useState<number | null>(null)
  const [isLoading, setIsLoading] = useState(true)
  const supabase = createBrowserClient()

  useEffect(() => {
    const fetchLimits = async () => {
      try {
        const { data: downloads, error: downloadError } = await supabase.rpc("get_remaining_downloads", {
          p_user_id: userId,
        })

        const { data: uploads, error: uploadError } = await supabase.rpc("get_remaining_uploads", {
          p_user_id: userId,
        })

        if (!downloadError && downloads !== null) {
          setRemainingDownloads(downloads)
        }
        if (!uploadError && uploads !== null) {
          setRemainingUploads(uploads)
        }
      } catch (error) {
        console.warn("[v0] Could not fetch limits:", error)
      } finally {
        setIsLoading(false)
      }
    }

    fetchLimits()
  }, [userId, supabase])

  if (isLoading) {
    return null
  }

  const downloadLimit = userPlan === "free" ? 10 : userPlan === "de-0-a-hit" ? 100 : null
  const uploadLimit = userPlan === "free" ? 1 : userPlan === "de-0-a-hit" ? 10 : null

  return (
    <Card className="p-6 border-2 border-border bg-card rounded-2xl">
      <h3 className="font-bold text-foreground mb-6 text-lg">Tus límites</h3>

      <div className="space-y-4">
        {/* Downloads section */}
        <Link href="/plans">
          <button className="w-full p-4 rounded-xl bg-accent/50 border border-border hover:border-primary/50 transition-all text-left">
            <div className="flex items-center justify-between mb-2">
              <div className="flex items-center gap-2">
                <Download className="h-5 w-5 text-primary" />
                <span className="font-semibold text-foreground">Descargas restantes este mes</span>
              </div>
              {downloadLimit === null ? (
                <span className="text-2xl font-black text-primary">∞</span>
              ) : (
                <span className="text-2xl font-black text-primary">{remainingDownloads ?? downloadLimit}</span>
              )}
            </div>
            <p className="text-sm text-muted-foreground">
              {downloadLimit === null
                ? "Plan Studio Plus: descargas ilimitadas"
                : `Tu plan permite ${downloadLimit} descargas/mes`}
            </p>
          </button>
        </Link>

        {/* Uploads section */}
        <Link href="/plans">
          <button className="w-full p-4 rounded-xl bg-accent/50 border border-border hover:border-secondary/50 transition-all text-left">
            <div className="flex items-center justify-between mb-2">
              <div className="flex items-center gap-2">
                <Upload className="h-5 w-5 text-secondary" />
                <span className="font-semibold text-foreground">Packs que puedes subir</span>
              </div>
              {uploadLimit === null ? (
                <span className="text-2xl font-black text-secondary">∞</span>
              ) : (
                <span className="text-2xl font-black text-secondary">{remainingUploads ?? uploadLimit}</span>
              )}
            </div>
            <p className="text-sm text-muted-foreground">
              {uploadLimit === null ? "Plan Studio Plus: packs ilimitados" : `Tu plan permite ${uploadLimit} pack(s)`}
            </p>
          </button>
        </Link>

        {/* Upgrade CTA */}
        {userPlan === "free" && (remainingDownloads === 0 || remainingUploads === 0) && (
          <div className="p-4 rounded-xl bg-primary/10 border border-primary/30">
            <p className="text-sm text-foreground mb-3 font-semibold">¿Necesitás más límites?</p>
            <Link href="/plans" className="block">
              <Button size="sm" className="w-full h-10 gap-2 rounded-lg bg-primary hover:bg-primary/90">
                <Zap className="h-4 w-4" />
                Mejorar Plan
              </Button>
            </Link>
          </div>
        )}

        {userPlan === "de-0-a-hit" && (remainingDownloads === 0 || remainingUploads === 0) && (
          <div className="p-4 rounded-xl bg-purple-500/10 border border-purple-500/30">
            <p className="text-sm text-foreground mb-3 font-semibold">¿Querés límites ilimitados?</p>
            <Link href="/plans" className="block">
              <Button size="sm" className="w-full h-10 gap-2 rounded-lg bg-purple-500 hover:bg-purple-600">
                <Crown className="h-4 w-4" />
                Studio Plus
              </Button>
            </Link>
          </div>
        )}
      </div>
    </Card>
  )
}
